.preorder {
    padding-top: 53px;
    border-top: 1px solid #E6E6E6;
    background-color: #f7f7f4;
    color: #2D3039; }

.preorder {
    padding-top: 52px; }

.preorder__title {
    width: 590px;
    margin: 0 auto;
    font-size: 40px;
    line-height: 50px;
    text-align: center; }

.preorder__desc {
    font-size: 18px;
    line-height: 1.44;
    text-align: center;
    padding-bottom: 23px;
    span {
        display: block; } }

.preorder__form {
    margin-top: 58px;
    margin-left: 0;
    display: flex;
    align-items: flex-end; }

.preorder__form-label {
    display: flex;
    flex-direction: column;
    font-size: 14px;
    text-transform: uppercase;
    width: 30%;
    margin-right: 10px;
    margin-bottom: 0; }

.preorder__form-input {
    height: 50px;
    margin-top: 6px;
    padding-left: 20px;
    font-size: 16px;
    border: 1px solid #e6e6e6;
    border-radius: 3px; }

.preorder__button {
    min-width: 300px;
    width: 300px;
    height: 50px;
    margin-left: 20px;
    padding: 0;
    font-family: inherit;
    font-size: 18px;
    color: #fff;
    background-color: #0278ff;
    border: none;
    border-radius: 3px;
    position: relative;
    cursor: pointer; }

