.feature {
  padding-top: 86px; }

.feature__title {
  font-size: 40px;
  line-height: 52px;
  text-align: center; }

.feature__list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 11px;
  margin-top: 55px; }

.feature__elem {
  display: flex;
  flex-direction: column;
  padding: 35px 20px 20px 24px;
  border: 1px solid #e6e6e6;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.05);
  border-radius: 3px;

  &:nth-child(2) {
    .feature__elem-title {
      &::before {
        background-image: url('../img/icon/icon-report.svg'); } } }

  &:nth-child(3) {
    .feature__elem-title {
      &::before {
        background-image: url('../img/icon/icon-analysis.svg'); } } }

  &:nth-child(4) {
    .feature__elem-title {
      &::before {
        background-image: url('../img/icon/icon-connection.svg'); } } }

  &:nth-child(5) {
    .feature__elem-title {
      &::before {
        background-image: url('../img/icon/icon-monitoring.svg'); } } }

  &:nth-child(6) {
    .feature__elem-title {
      &::before {
        background-image: url('../img/icon/icon-contract.svg'); } } } }

.feature__elem-title {
  position: relative;
  margin: 0;
  padding-top: 65px;
  font-size: 20px;
  font-weight: 600;
  line-height: 28px;
  color: $basic-green;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 50px;
    height: 50px;
    background: url('../img/icon/icon-loupe.svg') no-repeat center;
    background-size: contain; } }

.feature__elem-desc {
  margin: 0;
  font-size: 16px;
  line-height: 24px; }

.feature__elem-link {
  display: block;
  margin-top: auto;
  margin-right: 10px;
  font-size: 16px;
  line-height: 19px;
  text-align: right;
  color: $basic-gray;
  text-decoration: underline; }
