.source-modal__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 750px;
  height: 840px;
  padding: 14px 24px 45px 28px;
  background-color: #e6e6e6;
  overflow: hidden; }

.source-modal__title {
  margin: 0;
  font-size: 42px;
  line-height: 50px;
  font-weight: 600; }

.source-modal__list {
  height: 100%;
  overflow-y: scroll;
  margin: {}
  margin-top: 33px;
  font-size: 16px;
  line-height: 24px;

  &::-webkit-scrollbar {
    width: 12px; }

  &::-webkit-scrollbar-track {
      -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
      border-radius: 10px; }

  &::-webkit-scrollbar-thumb {
      border-radius: 10px;
      -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5); } }


.source-modal__list-item {
  width: 600px;
  margin-bottom: 5px; }

.source-modal__list-desc {
  margin: 0; }

.popup__button {
    position: absolute;
    top: 0;
    right: 0;
    margin: 0;
    padding: 0;
    width: 45px;
    height: 45px;
    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;
    transform: rotate(45deg);
    &::before {
        content: '';
        position: absolute;
        top: 50%;
        right: 50%;
        transform: translate(50%, 50%);
        width: 63px;
        height: 2px;
        background-color: #F8E07E; }
    &::after {
        content: '';
        position: absolute;
        top: 50%;
        right: 50%;
        transform: translate(50%, 50%) rotate(90deg);
        width: 63px;
        height: 2px;
        background-color: #F8E07E; } }
