.popup {
    position: absolute;
    z-index: 101;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    width: 100%;
    height: 100%;
    background-color: rgba(53, 53, 53, .95);
    display: none; }

.popup__content {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 975px;
    height: 689px;
    margin: auto;
    text-align: center;
    background-color: red;
    background: url('../img/popup-bg.png') no-repeat center bottom; }

.popup__text {
    width: 735px;
    margin: 0;
    margin-top: 60px;
    font-size: 60px;
    line-height: 115%;
    color: #dcdc70; }

.source-modal__button {
    position: absolute;
    top: 20px;
    right: 20px;
    margin: 0;
    padding: 0;
    width: 45px;
    height: 45px;
    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;
    transform: rotate(45deg);
    &::before {
        content: '';
        position: absolute;
        top: 50%;
        right: 50%;
        transform: translate(50%, 50%);
        width: 45px;
        height: 2px;
        background-color: $basic-gray; }
    &::after {
        content: '';
        position: absolute;
        top: 50%;
        right: 50%;
        transform: translate(50%, 50%) rotate(90deg);
        width: 45px;
        height: 2px;
        background-color: $basic-gray; } }
