.rate {
  margin-top: 77px;
  background-color: #f7f7f4; }

.rate__title {
  padding-top: 70px;
  font-size: 50px;
  line-height: 82px;
  text-align: center; }

td {
  // border: 1px solid #000
  border-collapse: collapse; }

//
.rate__table {
  margin-top: 50px; }

.table {
  width: 100%;
  border-collapse: collapse; }

.table-data--head {
  width: 158px;
  padding-left: 10px;
  vertical-align: top;

  &:first-child {
    width: auto; } }

.table-data__head-title {
  margin: 0;
  font-size: 22px;
  line-height: 32px;
  font-weight: 500;
  color: $basic-green; }

.table-data__price {
  margin: 0;
  font-size: 27px;
  line-height: 32px;
  font-weight: 600;

  &--vip {
    color: $basic-green; } }

.table-data__month {
  margin: 0;
  font-size: 14px;
  line-height: 24px; }

// inner table

.table-data__content {
  padding: 10px 0 10px 15px;
  border: 1px solid #E6E6E6;
  border-width: 1px 0;

  &.active {
    background-color: $white; } }

.data-content__title {
  display: flex;
  align-items: center;
  width: auto;
  font-size: 16px;
  line-height: 24px;
  font-weight: 600; }

.data-content__circle {
  position: relative;
  width: 158px;

  &::before {
    content: '';
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 10px;
    width: 16px;
    height: 16px;
    background-color: $basic-gray;
    border-radius: 50%; }

  &--green {
    &::before {
      background-color: $basic-green; } } }

.data-content__desc {
  width: 707px;
  margin: 0;
  padding-right: 80px;
  font-size: 16px;
  line-height: 24px;
  display: none; }

.data-content__button {
  width: 20px;
  height: 20px;
  margin-left: 10px;
  padding: 0;
  border: 0;
  background: url(../img/icon/icon-arrow.svg) no-repeat center;
  cursor: pointer;
  outline: none; }

.table-data--button {
  padding-right: 18px; }

.table-data__link {
  width: 100%;
  display: inline-block;
  margin-top: 20px;
  padding: 10px 0;
  font-size: 16px;
  font-weight: 300;
  line-height: 19px;
  text-align: center;
  color: #fff;
  background-color: $basic-gray;
  border-radius: 3px;

  &--green {
    background-color: $basic-green; } }
