.helper {
	padding-top: 50px; }

.helper__header {
	display: flex;
	align-items: center;
	border-bottom: 1px solid #e6e6e6; }

.helper__title {
	width: 580px;
	margin-top: 20px; }

.helper__pic {
	width: 377px;
	height: 256px;
	margin-left: 65px; }

.helper__list {
	display: flex;
	flex-direction: column;
	width: 890px;
	margin: 50px auto 0; }

.helper-item {
	display: flex;
	justify-content: space-between;
	margin-bottom: 30px;

	&:last-child {
		margin-bottom: 0; }

	&:nth-child(2) {
		.helper-item__desc {
			&::before {
				width: 57px;
				height: 90px;
				background-image: url('../img/icon/icon-risk.svg');
				background-size: contain; } } }

	&:nth-child(3) {
		.helper-item__desc {
			&::before {
				top: 20px;
				width: 50px;
				height: 69px;
				background-image: url('../img/icon/icon-analysis.svg');
				background-size: contain; } } } }

.helper-item__desc {
	position: relative;
	width: 448px;
	margin: 0;
	padding-left: 100px;
	padding-top: 17px;
	font-size: 20px;
	font-weight: 600;
	line-height: 31px;
	color: #8893b3;

	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 72px;
		height: 72px;
		background: url('../img/icon/icon-best.svg') no-repeat center;
		background-size: cover; } }

.helper-item__list {
	width: 380px;
	margin-right: 20px; }

.helper-item__list-elem {
	padding-top: 17px; }

.helper-item__list-desc {
	position: relative;
	margin: 0;
	margin-bottom: 13px;
	padding-left: 30px;
	font-size: 16px;
	line-height: 24px;

	&:last-child {
		margin-bottom: 0; }

	&::before {
		content: '—';
		position: absolute;
		top: 0;
		left: 0; } }

.helper__button {
	margin: 55px auto 0; }
