.about {
  padding-top: 48px; }

.about__title {
  width: 590px;
  font-size: 40px;
  line-height: 52px;
  letter-spacing: -0.1px; }

.about__content {
  display: flex;
  justify-content: space-between;
  margin-top: 15px;
  border-bottom: 1px solid #E6E6E6; }

.about__content-left {
  width: 505px;
  margin-top: 45px; }

.about__content-right {
  width: 554px;
  height: 998px;
  overflow: hidden; }

.about-list__item {
  position: relative;
  margin-bottom: 10px;
  padding-left: 49px;

  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 45px;
    height: 37px;
    background: url('../img/icon/icon-risk.svg') no-repeat center; }

  &:nth-child(2) {
    &:after {
      background-image: url('../img/icon/icon-docs.svg'); } }

  &:nth-child(3) {
    &:after {
      background-image: url('../img/icon/icon-template.svg'); } }

  &:nth-child(4) {
    &:after {
      background-image: url('../img/icon/icon-tax.svg'); } }

  &:nth-child(5) {
    &:after {
      background-image: url('../img/icon/icon-rating.svg'); } }

  &:nth-child(6) {
    &:after {
      background-image: url('../img/icon/icon-resources.svg'); } }

  &:nth-child(7) {
    &:after {
      background-image: url('../img/icon//icon-debt.svg'); } }

  &:nth-child(8) {
    &:after {
      background-image: url('../img/icon/icon-lockout.svg'); } }

  &::before {
    content: '';
    position: absolute;
    z-index: 2;
    top: 15px;
    right: -84px;
    width: 130px;
    height: 0.5px;
    background-color: $basic-green;
    transition: 0.2s;
    opacity: 0; }

  &.active {
    .about-list__link {
      border-bottom: 1px dashed $basic-green; }

    &::before {
      opacity: 1; } } }



.about-list__link {
  font-size: 20px;
  font-weight: 600;
  line-height: 28px;
  color: $basic-green; }

.about-list__desc {
  width: 425px;
  margin: 0;
  font-size: 16px;
  line-height: 24px; }

.about__content-button {
  margin-left: 49px;
  margin-bottom: 25px; }

.about__content-image {
  transition: 0.5s; }
