.source {
  margin-top: 102px; }

.source__title {
  font-size: 40px;
  line-height: 52px;
  text-align: center; }

.source__list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  margin-top: 75px; }

.source__item {
  position: relative;
  padding-top: 95px;
  padding-bottom: 28px;
  border-right: 1px solid #e6e6e6;
  border-bottom: 1px solid #e6e6e6;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 75px;
    height: 75px;
    background: url('../img/icon/icon-fns.svg') no-repeat center;
    background-size: contain; }

  &:nth-child(1n + 4) {
    border-bottom: none;
    padding: 0;
    padding-top: 120px;

    &::before {
      top: 45px; } }

  &:nth-child(5) {
    &::before {
      background-image: url('../img/icon/icon-rfp.svg'); } }

  &:nth-child(6) {
    &::before {
      background-image: url('../img/icon/icon-fssp.svg'); } } }

.source__item-desc {
  margin: 0;
  margin-top: 15px;
  font-size: 16px;
  line-height: 24px;
  text-align: center;

  span {
    display: block; } }

.source__button {
  display: block;
  width: 452px;
  height: 60px;
  margin: 50px auto 0;
  padding: 0;
  font-family: inherit;
  font-size: 21px;
  line-height: 26px;
  color: #6e7992;
  background-color: transparent;
  border: 1px solid #6E7992;
  border-radius: 3px;
  cursor: pointer;
  outline: none; }
