
.hero__header {
  display: flex;
  align-items: flex-end;
  padding: 39px 0 21px; }

.hero__header-list {
  display: flex;
  margin-left: auto;
  line-height: 0; }

.hero__header-item {
  margin-left: 34px;
  &:first-child {
    margin-left: 0; } }

.hero__header-link {
  font-size: 18px;
  font-weight: 600;
  line-height: 28px;
  color: #6E7992; }

.hero__content {
  position: relative;
  background-color: #f7f7f4;

  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 30px;
    background-color: $white; } }

.hero__content-wrapper {
  padding: 96px 0 70px 0;
  position: relative;

  &:after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 429px;
    height: 100%;
    background: url('../img/hero-bg.svg') no-repeat right bottom; } }

.hero__title {
  font-size: 53px;
  line-height: 60px;
  width: 670px; }

.hero__desc {
  width: 490px;
  margin: 0;
  margin-top: 21px;
  font-size: 18px;
  line-height: 34px; }

.hero__form {
  display: flex;
  justify-content: space-between;
  width: 666px;
  height: 55px;
  margin-top: 30px; }

.hero__form-label {
  width: 100%; }

.hero__form-input {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 0;
  padding-right: 10px;
  padding-left: 43px;
  font-size: 18px;
  font-weight: 300;
  line-height: 24px;
  color: #8893b3;
  background-color: $white;
  border: 1px solid #e6e6e6;
  border-right: 0;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.05);
  outline: none;

  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 17px;
    height: 17px;
    background: url('../img/icon/icon-search.svg') no-repeat center; } }

.hero__form-button {
  min-width: 203px;
  padding: 0;
  font-family: inherit;
  font-size: 18px;
  line-height: 22px;
  font-weight: 300;
  background-color: $basic-green;
  color: $white;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  outline: none; }
