.api {
  padding-top: 90px;
  overflow: hidden;
  background-color: #f7f7f4; }

.api__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 90px 0 25px;
  border: 1px solid #E6E6E6;
  border-radius: 3px;
  background-color: $white;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.05);

  &::before {
    content: '';
    position: absolute;
    top: -121px;
    left: 50%;
    transform: translateX(-50%);
    width: 206px;
    height: 206px;
    border-radius: 50%;
    background-color: #f7f7f4;
    box-shadow: 0px 2px 0 rgba(0,0,0,.05); }

  &::after {
    content: '';
    position: absolute;
    top: -50px;
    left: 50%;
    transform: translateX(-50%);
    width: 140px;
    height: 115px;
    background: url('../img/robot_intro.svg') no-repeat center; } }

.api__title {
  margin: 0;
  font-size: 27px;
  font-weight: 700;
  line-height: 31px;
  text-align: center;
  color: $basic-green; }

.api__desc {
  width: 885px;
  margin: 0 auto;
  font-size: 20px;
  line-height: 31px;
  text-align: center; }

.api__link {
  display: inline-block;
  margin: 10px auto 0;
  font-size: 20px;
  line-height: 20px;
  text-align: center;
  color: $basic-blue;
  box-shadow: 0 1px 0 $basic-blue; }
